import * as React from "react";
import "./styles.css";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import AlignItems from "./riyue/riyue.js";
import AlignItems22 from "./riyue/riyue2.js";
import AlignItems1 from "./mondo/mondo.js";
import AlignItems12 from "./mondo/mondo2.js";
import AlignItems3 from "./inazuma/inazuma.js";
import AlignItems32 from "./inazuma/inazuma2.js";
import AlignItems4 from "./sumeru/sumeru.js";
import AlignItems42 from "./sumeru/sumeru2.js";


interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`
  };
}

export default function BasicTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
        >
          <Tab label="モンド" {...a11yProps(0)} />
          <Tab label="璃月" {...a11yProps(1)} />
          <Tab label="稲妻" {...a11yProps(2)} />
          <Tab label="スメール" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <h2>星5</h2>
        <AlignItems1 />
        <h2>星4</h2>
        <AlignItems12 />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <h2>星5</h2>
        <AlignItems />
        <h2>星4</h2>
        <AlignItems22 />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <h2>星5</h2>
        <AlignItems3 />
        <h2>星4</h2>
        <AlignItems32 />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <h2>星5</h2>
        <AlignItems4 />
        <h2>星4</h2>
        <AlignItems42 />
      </TabPanel>
    </Box>
  );
}
