import * as React from "react";
import Box, { BoxProps } from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import menu from "../json/menu2";
import SwitchLabels from "../material/switch";
import ImageAvatars from "../zokusei/kaze";
import ImageAvatars2 from "../zokusei/iwa";
import ImageAvatars3 from "../zokusei/kaminari";
import ImageAvatars4 from "../zokusei/kori";
import ImageAvatars5 from "../zokusei/mizu";
import ImageAvatars6 from "../zokusei/hi";
import ImageAvatars7 from "../zokusei/kusa";
import BasicRating from "../material/totu";

function Item(props: BoxProps) {
  const { sx, ...other } = props;
  return (
    <Box
      sx={{
        p: 1,
        m: 1,
        bgcolor: (theme) =>
          theme.palette.mode === "dark" ? "#101010" : "grey.100",
        color: (theme) =>
          theme.palette.mode === "dark" ? "grey.300" : "grey.800",
        border: "1px solid",
        borderColor: (theme) =>
          theme.palette.mode === "dark" ? "grey.800" : "grey.300",
        borderRadius: 2,
        fontSize: "0.875rem",
        fontWeight: "700",
        ...sx
      }}
      {...other}
    />
  );
}

export default function AlignItems42() {
  return (
    <div style={{ width: "100%" }}>
      <Box
        sx={{
          display: "flex",
          flexWrap: "wrap",
          p: 1,
          m: 1,
          bgcolor: "background.paper",
          maxWidth: 1800,
          borderRadius: 1
        }}
      >
        {/*コレイ*/}
       <Item> <Card sx={{ maxWidth: 220 }}>
          <CardMedia
            component="img"
            alt="green iguana"
            height="220"
            img
            src={menu[25]["写真"]}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              <>{menu[25]["name"]}</>
            </Typography>
            <Typography variant="body2" color="text.secondary"></Typography>
            <ImageAvatars7 />
            <BasicRating />
            <SwitchLabels />
          </CardContent>
        </Card></Item>
        {/* キャディス */}

       <Item> <Card sx={{ maxWidth: 220 }}>
          <CardMedia
            component="img"
            alt="green iguana"
            height="220"
            img
            src={menu[26]["写真"]}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              <>{menu[26]["name"]}</>
            </Typography>
            <Typography variant="body2" color="text.secondary"></Typography>
            <ImageAvatars5 />
            <BasicRating />
            <SwitchLabels />
          </CardContent>
        </Card></Item>
        {/* レイラ */}
        <Item><Card sx={{ maxWidth: 220 }}>
          <CardMedia
            component="img"
            alt="green iguana"
            height="220"
            img
            src={menu[27]["写真"]}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              <>{menu[27]["name"]}</>
            </Typography>
            <Typography variant="body2" color="text.secondary"></Typography>
            <ImageAvatars4 />
            <BasicRating />
            <SwitchLabels />
          </CardContent>
        </Card></Item>
        {/* ファルザン */}
        <Item><Card sx={{ maxWidth: 220 }}>
          <CardMedia
            component="img"
            alt="no image"
            height="220"
            img
            src={menu[28]["写真"]}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              <>{menu[28]["name"]}</>
            </Typography>
            <Typography variant="body2" color="text.secondary"></Typography>
            <ImageAvatars />
            <BasicRating />
            <SwitchLabels />
          </CardContent>
        </Card></Item>

        
      </Box>
    </div>
  );
}
